@font-face {
  font-family: "MerriweatherSans";
  src: url("../font/MerriweatherSans-Regular.woff2") format("woff2"), url("../font/MerriweatherSans-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote {
  margin: 0;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

textarea {
  resize: none;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

html,
body {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
}

.main {
  flex-grow: 1;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

a,
input,
textarea,
button {
  outline: 0;
  border: 0;
}

button {
  background-color: transparent;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  width: 2px;
  height: 2px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 5px;
}

.container {
  max-width: 1270px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "MerriweatherSans", "Helvetica", "Arial", sans-serif;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.7;
  color: #fff;
  letter-spacing: 0.03em;
  background-color: #070a01;
}

.title {
  font-weight: 400;
  line-height: 1.2;
  font-size: 50px;
  text-transform: uppercase;
}

.logo__img {
  margin-right: 30px;
}

.logo__text {
  text-transform: uppercase;
}

.hero__title {
  font-weight: 400;
  font-size: 200px;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #070a01;
  background-color: #fff;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;
}

.btn-bg:hover {
  color: #fff;
  box-shadow: 0 0 0 1px #fff;
  background-color: #070a01;
}

.btn-bg:focus {
  color: #070a01;
  background-color: #fff;
}

.btn-bg:active {
  color: #fff;
  background-color: #070a01;
}

.header__nav {
  padding: 39px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__item:first-child {
  margin-right: 45px;
}

.menu__link {
  text-transform: uppercase;
  position: relative;
}

.menu__link::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  left: 0;
  bottom: 0;
  background-color: #fff;
  transition: opacity 0.3s ease-in-out;
}

.menu__link:hover:after {
  opacity: 0;
}

.hero__content {
  min-height: 655px;
  background-image: url(../img/decore/image6.png);
  background-repeat: no-repeat;
  background-position: right center;
  background-size: 556px 655px;
  display: flex;
  flex-direction: column;
}

.hero__text {
  font-size: 20px;
  max-width: 497px;
  margin-bottom: 67px;
}

.hero__btn {
  max-width: 288px;
  padding: 9px 112px;
  text-transform: uppercase;
}

.quotes {
  padding: 134px 0;
}

.quotes__description {
  display: flex;
}

.footer__content {
  padding: 33px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__copy {
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
}

@media (max-width: 1130px) {
  body {
    font-size: calc(16px + 0 * ((100vw - 375px) / 1270));
  }

  .title {
    font-size: calc(24px + 44.2 * ((100vw - 375px) / 1270));
  }

  .hero__title {
    font-size: calc(50px + 255 * ((100vw - 375px) / 1270));
  }
}